// BUTTONS
@mixin btn-style($background:$blue, $color:$white) {
  @include button(
    $bg: $background
  );
  padding: rem-calc(16*2/3) rem-calc(20*2/3);
  border: 1px solid $background;
  color: $color;
  font-size: rem-calc(24*2/3);
  line-height: 1;
  vertical-align: middle;
  text-align: center;

  @include focus();

  &:focus,
  &:hover,
  &:active,
  &.active {
    background-color: $background;
    color: $color;
  }

  .no-touch &:hover {
    background-color: $color;
    color: $background;
  }

  .icon-arrow,
  .icon-expand {
    margin-left: rem-calc(10);
    vertical-align: middle;
    &:before {
      line-height: 0;
    }
  }

  .icon-arrow {
    &.down {
      @include transform(rotate(90deg));
    }

    &.left {
      @include transform(rotate(180deg));
      margin-right: rem-calc(10);
      margin-left: 0;
      margin-top: rem-calc(5);
    }
  }

  @media #{$medium-up} {
    padding: rem-calc(18) rem-calc(24);
    font-size: rem-calc(16);
  }

  @media #{$small-only} {
    &.btn-big-mobile {
      padding: rem-calc(25*2/3) rem-calc(20*2/3);
      font-size: rem-calc(28*2/3);
    }
  }
}


// BTN TYPE
.btn-primary {
  @include btn-style($blue, $white);
}

.btn-secondary {
  @include btn-style($jet, $white);
  @include transition-property(background-color);
  @include transition-duration(.2s);
  @include transition-timing-function(ease-in-out);
}

.btn-white {
  @include btn-style($white, $mediumGrey);
  border-color: $mediumGrey;
  color: $jet;
  font-family: $font-regular;
  font-weight: $font-weight-normal;
  padding: rem-calc(18*2/3) rem-calc(22*2/3);

  @media #{$medium-up} {
    padding: rem-calc(15) rem-calc(20);
    font-size: rem-calc(13);
  }

  &:active,
  &:focus,
  &:hover {
    color: $jet;
  }

  .icon {
    color: $blue;
  }

  .no-touch &:hover {
    color: $jet;
  }
}

.btn-orange {
  @include btn-style($orange, $white);
  border-color: $orange;
  color: $white;
  padding: rem-calc(18*2/3) rem-calc(22*2/3);

  @media #{$medium-up} {
    padding: rem-calc(15) rem-calc(20);
    font-size: rem-calc(13);
  }

  &:active,
  &:focus,
  &:hover {
    color: $orange;
    border-color: $orange;
  }

  .icon {
    color: $orange;
  }

  .no-touch &:hover {
    color: $orange;
  }
}

// FILTERS
.active {
  .btn-filter {
    border-color: $blue;
    color: $blue;
  }
}
.btn-filter {
  background: $white;
  font-size: $button-font-med;
  font-family: $font-bold;
  font-weight: $font-weight-normal;
  border: solid 3px $lightBlue;
  color: $lightBlue;
  padding: $button-sml ($button-lrg * 2);
  display: inline-block;
  @include transition-property(border-color, color);
  @include transition-duration(.2s);
  @include transition-timing-function(ease-in-out);

  .no-touch &:hover,
  &.active {
    border-color: $blue;
    color: $blue;
  }

  @include focus();

  @media #{$medium-up} {
    font-size: $button-font-lrg;
    padding: rem-calc(10) rem-calc(20);
  }

  &.alternative {
    border: solid 1px $blue;
    color: $blue;
    letter-spacing: rem-calc(2);

    .no-touch &:hover,
    &.active {
      background-color: $blue;
      color: $white;
    }

    &:hover {
      background-color: $blue;
      color: $white;
      opacity: .5;
    }
  }
}


// BUTTON WITH ICON
.btn-icon {
  position: relative;
  .button__content {
    display: inline-block;
    position: relative;
    padding-left: rem-calc(40);
  }

  .icon {
    vertical-align: middle;
    @include position(absolute, 50% null null 0);
    @include transform(translateY(-50%));
    font-size: 200%;
  }
}

.btn-icon--only {
  @include btn-style($white, $mediumGrey);
  border-color: $mediumGrey;
  color: $blue;
  font-weight: $font-weight-normal;
  width: rem-calc(60*2/3);
  height: rem-calc(60*2/3);
  padding: 0;
  @include display(flex);
  @include align-items(center);

  span {
    margin: auto;
    &:before {
      font-size: rem-calc(30);
    }
  }

  @include focus();

  &:active,
  &:focus,
  &:hover {
    color: $blue;
  }

  .no-touch &:hover,
  &.active {
    color: $jet;
    background: $mediumGrey;
  }

  @media #{$medium-up} {
    padding: 0;
    width: rem-calc(45);
    height: rem-calc(45);

    span:before {
      font-size: rem-calc(40);
    }
  }
}


// PHONE
.btn-phone {
  @include btn-style($white, $blue);
  border: 1px solid $blue;
  box-shadow: inset 0 0 0 2px $blue;
}
.btn-phone-green {
  @include btn-style($white, $green);
  border: 1px solid $green;
  box-shadow: inset 0 0 0 2px $green;
  span {
    opacity: .5;
  }

  &:focus,
  &:hover {
    background-color: transparent;
    color: $green;
    box-shadow: inset 0 0 0 2px $green;
  }
}
.btn-phone,
.btn-phone-green {
  background: transparent;
  border-radius: 4px;
}
@media #{$medium-up} {
  .btn-phone,
  .btn-phone-green {
    font-size: rem-calc(16);
    padding-top: rem-calc(13);
    padding-bottom: rem-calc(14);
  }
}


// ROUNDED BUTTONS
.btn-rounded {
  border-radius: 4px;
  padding: rem-calc(18*2/3) rem-calc(22*2/3);
  font-size: rem-calc(20*2/3);

  @media #{$medium-up} {
    padding: rem-calc(15) rem-calc(20);
    font-size: rem-calc(13);
  }
}


// btn wrapper
.btn-wrapper {
  text-align: center;
  &.btn-wrapper--left {
    @media #{$medium-up} {
      text-align: left;
    }

    > * {
      @media #{$small-only} {
        width: 100%;
      }
    }
  }
  &.btn-wrapper--center {
    > * + * {
      margin-left: rem-calc(20);
    }

    @media #{$medium-up} {
      text-align: center;
    }

    > * {
      margin-bottom: 0;
      @media #{$small-only} {
        width: 100%;
        margin-bottom: rem-calc(20*2/3);

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

.icon-arrow {
  margin-left: rem-calc(10);
  vertical-align: inherit;
  @include transform(rotate(1deg));

  &:before {
    line-height: 0;
  }

  &.down {
    @include transform(rotate(90deg));
  }

  &.up {
    @include transform(rotate(-90deg));
  }

  &.left {
    @include transform(rotate(180deg));
    margin-right: rem-calc(10);
    margin-left: 0;
    margin-top: rem-calc(5);
  }
}