// SELECT-LIKE DROPDOWN
.form-dropdown {
  background: $input-bg-color;
  border: $input-border-width $input-border-style $input-border-color;
  border-radius: $input-border-radius;
  color: $input-font-color;
  margin-bottom: $form-spacing;
  min-height: $form-height;
  position: relative;

  &__trigger {
    @extend .btn-unstyled;
    position: relative;
    width: 100%;
    min-height: $form-height;
    padding: $form-padding rem-calc(40) $form-padding $form-padding;
    font-weight: $form-label-font-weight;
    font-size: $input-font-size;
    margin: 0;
    line-height: 1;
    @include focus();

    .icon-expand {
      @include position(absolute, 50% 0 null null);
      @include transform(translateY(-50%));
      @include transition-property(all);
      @include transition-duration(.4s);
      @include transition-timing-function(ease-out);
      color: $blue;
      font-size: ($input-font-size * 1.35);
      margin-right: $form-padding;
    }

    &[aria-expanded="true"] {
      .icon-expand {
        @include transform(rotate(180deg) translateY(50%));
      }
    }
  }

  &__wrapped {
    @include display(flex);
    @include align-items(center);

    .form-dropdown__trigger {
      border: none !important;
      width: 0 !important;
      padding: rem-calc(15) rem-calc(10);
      margin-right: rem-calc(10);
      height: 0 !important;
      min-height: 0;

      .icon-expand{
        font-size: rem-calc(15);
        margin-right: 0;
        @include transform(rotate(-90deg)  translateX(50%));
        @include transition-duration(.2s);
      }
      &[aria-expanded="true"] {
        .icon-expand {
          @include transform(rotate(0deg)  translateY(-50%));
        }
      }
    }
  }

  &__content {
    padding: $form-padding $form-padding 0 $form-padding;
    background: $input-bg-color;

    a {
      color: $jet;
      padding: $form-padding;
    }

    .undo-padding {
      margin-top: (-$form-padding);
    }

    position: absolute;
    z-index: 1;
    background: $input-bg-color;
    border: $input-border-width $input-border-style $input-border-color;
    width: rem-calc(270);
    box-shadow: rem-calc(3) rem-calc(3) rem-calc(3) 0 rgba($black, .2);

    .bordered {
      position: relative;

      @media #{$medium-up} {
        padding: rem-calc(10) 0;
      }

      > * {
        display: block;
        margin-top: 0;
        margin-bottom: 0;
      }

      a {
        padding: $form-padding;
        margin-left: (-$form-padding);
        margin-right: (-$form-padding);
      }

      &:after {
        content: "";
        @include position(absolute, null 0 0 0);
        height: 1px;
        background-color: $input-border-color;
      }

      &:last-child {
        &:after {
          display: none;
        }
      }
    }

    .label-checkbox {
      display: block;
    }
  }

  @media #{$medium-up} {
    &.form-dropdown--responsive {
      background: transparent;
      border: none;
      padding: 0;
      margin-bottom: rem-calc(30);

      .form-dropdown__trigger {
        display: none;
      }

      .form-dropdown__content {
        display: block;
        padding: 0;

        .undo-padding {
          margin-top: 0;
        }

        position: relative;
        background: transparent;
        border: none;
        width: 100%;
        box-shadow: none;

        > * {
          @include display(flex);
          @include justify-content(center);

          .bordered {
            @include display(flex);
            @include align-items(stretch);
            @include flex(1 1 0%);

            &:after {
              display: none;
            }

            margin-right: rem-calc(10);
            &:last-child {
              margin-right: 0;
            }

            > * {
              @include display(flex);
              @include align-items(center);
              @include justify-content(center);
              width: 100%;
              padding: rem-calc(16*2/3) rem-calc(20*2/3);
              border: 1px solid $blue;
              color: $blue;
              font-size: rem-calc(24*2/3);
              line-height: 1;
              vertical-align: middle;
              margin: 0;
              font-family: $font-bold;
              text-align: center;

              &:focus,
              &:hover,
              &:active,
              &.active {
                background-color: $blue;
                color: $white;
              }
            }
          }
        }
      }
    }
  }
}


// "?" picto
.infotip {
  position: relative;
  display: inline-block;
  span { @extend .visually-hidden; }

  &:after {
    content: "?";
    cursor: pointer;
    display: inline-block;
    width: rem-calc(20);
    height: rem-calc(20);
    border-radius: 50%;
    background: $blue;
    color: $white;
    line-height: 1.3;
    text-align: center;
    font-size: rem-calc(15);
  }
}
_:-ms-lang(x), .infotip:after { line-height: 1.4; } // IE10 and IE11 hack


// Button dropdown
[data-dropdown] {
  .icon-expand {
    @include transition-property(all);
    @include transition-duration(.4s);
    @include transition-timing-function(ease-out);
    &:after {
      @include position(absolute, 50% 0 null null);
    }
  }

  &[aria-expanded="true"] {
    .icon-expand {
      @include transform(rotate(180deg));
    }
  }
}

.f-dropdown {
  padding: rem-calc(10);
  opacity: 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .5);
  @include transform(translateY(-10px));
  @include transition-property(opacity, transform);
  @include transition-duration(.3s);
  @include transition-timing-function(ease-out);

  &.f-open-dropdown {
    opacity: 1;
    @include transform(translateY(0));
    outline: none;
  }
}


// AJAX
@include keyframes(sk-rotate) {
  100% {
    @include transform(rotate(360deg));
  }
}
@include keyframes(sk-bounce) {
  0%, 100% {
    @include transform(scale(0.0));
    opacity: .5;
  } 50% {
    @include transform(scale(1.0));
    opacity: 1;
  }
}

.ajax-wait {
  &,
  td[colspan="100%"] {

    > div {
      margin: rem-calc(40) auto;
      width: rem-calc(100);
      height: rem-calc(100);
      position: relative;
      text-align: center;
      @include animation(sk-rotate 2.0s infinite linear);

      &:before,
      &:after {
        content: " ";
        width: 60%;
        height: 60%;
        display: inline-block;
        position: absolute;
        top: rem-calc(10);
        background-color: rgba($grey, .4);
        border-radius: 100%;
        @include animation(sk-bounce 2.0s infinite ease-in-out);
      }

      &:after {
        top: auto;
        bottom: rem-calc(10);
        @include animation-delay(-.5s);
      }
    }
  }
}

.geoloc-wait {
  overflow: hidden;
  position: relative;

  div.geoloc-wait__spinner {
    @include position(absolute, 0 0 0 0);
    @include animation(sk-rotate 2.0s infinite linear);

    &:before,
    &:after {
      content: " ";
      width: rem-calc(100);
      height: rem-calc(100);
      @include position(absolute, 50% null null 50%)
      @include transform(translate(-50%, -50%))
      background-color: rgba($grey, .4);
      border-radius: 100%;
      @include animation(sk-bounce 2.0s infinite ease-in-out);
    }

    &:after {
      top: 45%;
      bottom: 45%;
      @include animation-delay(-.5s);
    }

    + * {
      opacity: 0;
    }
  }

  span.geoloc-wait__spinner {
    @include position(absolute, 0 0 0 0);
    @include animation(sk-rotate 2.0s infinite linear);

    &:before,
    &:after {
      content: " ";
      width: rem-calc(15);
      height: rem-calc(15);
      @include position(absolute, 50% null null 50%)
      @include transform(translate(-50%, -50%))
      background-color: rgba($grey, .6);
      border-radius: 100%;
      @include animation(sk-bounce 2.0s infinite ease-in-out);
    }

    &:after {
      top: 45%;
      bottom: 45%;
      @include animation-delay(-.5s);
    }

    + * {
      opacity: 0;
    }
  }
}


// FILTERS
.filter {
  @include display(flex);
  @include align-items(center);
  @include justify-content(flex-end);
  margin-top: rem-calc(20*2/3);

  &--left {
    @media #{$small-only} {
      @include justify-content(center);
      .form-dropdown__trigger {
        display: block;
        font-size: rem-calc(30*2/3);
        font-family: $font-bold;
      }
    }

    @media #{$medium-up} {
      @include justify-content(flex-start);
    }
  }

  > * {
    margin-bottom: 0;
  }

  .combo-select {
    display: inline-block;
    width: 65%;
    max-width: rem-calc(300);
    input[type=text] {
      border-width: 1px;
      margin-bottom: 0;
    }
  }

  .form-dropdown__content--last {
    right: 0;
  }

  &__label {
    font-family: $font-bold;
    text-align: right;
    margin-right: rem-calc(20);
    display: inline-block;
  }

  &__item {
    @include align-self(flex-end);
  }
}

.filter-aside {
  .filter {
    margin-top: rem-calc(40*2/3);
  }
}

.remove-item {
  background: $white;
  font-size: rem-calc(32px);
  border: 1px solid $mediumGrey;
}

.voir-plus {
  width: 100%;
  text-align: center;
  padding-top: $small-section-padding;

  a, button {
    margin-bottom: 0;
  }

  @media #{$medium-up} {
    padding-top: $medium-section-padding;
  }
}

// language component
.lang {
  position: absolute;
  top: 0;
  width: 100%;
  padding-top: rem-calc(67);

  ul {
    @include display(flex);
    float: right;
    list-style: none;

  }
  li {
    &.active a,
    a:hover {
      background-color: $white;
      color: $blue;
    }
    &:first-child {
      a {
        @include border-left-radius(rem-calc(5));
        border-right: 0;

        &:after {
          content: "";
          position: absolute;
          top: 50%;
          right: 0;
          width: 1px;
          height: rem-calc(20);
          margin-top: rem-calc(-10);
          position: absolute;
          border-right: 1px solid $grey;
        }
      }
    }
    &:last-child a {
      @include border-right-radius(rem-calc(5));
      border-left: 0;
    }
  }
  a {
    position: relative;
    z-index: 1;
    display: block;
    width: rem-calc(40);
    height: rem-calc(40);
    font-family: $font-bold;
    font-size: rem-calc(15);
    line-height: rem-calc(38);
    background-color: $white;
    border: 1px solid $grey;
    color: $grey;
    text-transform: uppercase;
    text-align: center;
  }
}

@media #{$medium-up} {
  .filter-aside {
    overflow: visible;
    width:100%;
    @include display(flex);
    @include align-items(center);

    .filter {
      margin-top: 0;
      margin-left: auto;
      @include flex-grow(2);

      .combo-select {
        max-width: rem-calc(200);
      }
    }
  }

  .lang {
    padding-top: rem-calc(87);
    li {
      &:first-child {
        a {
          border-top-left-radius: rem-calc(5);
        }
      }
    }
  }
}
@media #{$small-only} {
  .tooltip {
    right: rem-calc(15) !important;
    width: auto !important;
    max-width: none;
  }
}


// PAGINATION
.item-list {

  .pager {
    text-align: center;
    @include display(flex);
    @include justify-content(center);
    @include align-items(stretch);
    margin: 0;

    li {
      display: inline-block;
      vertical-align: middle;
      list-style: none;
      width: rem-calc(70*2/3);
      padding: rem-calc(20) 0;
      font-family: $font-bold;
      font-size: rem-calc(30*2/3);
      margin: 0;

      a {
        display: block;
      }
    }

    .first {
      margin-left: 0;
    }

    @media #{$medium-up} {
      li {
        padding: rem-calc(40) 0;
        width: rem-calc(70);
        font-size: rem-calc(30);
      }
    }
  }

}
