body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

iframe {
  max-width: 100%;
}

figure {
  margin: 0;
}

fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

label {
  font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
  vertical-align: baseline;
  font-family: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-size: 100%;
  outline: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

button {
  cursor: pointer;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  background: transparent;
  padding: inherit;
  text-align: inherit;
  border: none;
  display: inline-block;
  @include appearance(none);
  font-family: $font-regular;

  .no-touch &:hover,
  &:active {
    background: inherit;
    color: inherit;
  }

  @include focus();
}

