html,
body {
  text-rendering: geometricPrecision;
  -webkit-font-smoothing: antialiased;
}

a {
  @include focus();
  .no-touch &:hover {
    background-color: transparent;
  }

  &.textLink:hover {
    color: $blue;
  }
}

p {
  color: $grey;
  font-size: rem-calc(16);
  letter-spacing: 0.01em;
  line-height: 1.4;

  @media #{$medium-up} {
    line-height: 1.6;
  }
}

strong,
b,
.text-bold {
  font-family: $font-bold;
  font-weight: $font-weight-normal;
}

sup {
  font-size: 0.7em;
  line-height: 0;
}

.text-normal {
  font-family: $font-regular;
  font-weight: $font-weight-normal;
}

.text-italic {
  font-style: italic;
}


/* -------- */
/* HEADINGS */
/* -------- */

.heading {
  position: relative;
  padding-top: rem-calc(18);
  margin-bottom: rem-calc(40*2/3);

  &__title {
    font-family: $font-bold;
    font-weight: $font-weight-normal;
    line-height: 120%;
    font-size: rem-calc(40*2/3);
    margin: 0;
    color: $jet;
    display: block;
  }

  &__title--sub {
    color: $blue;
    font-size: rem-calc(30*2/3);
  }

  &__text {
    font-family: $font-bold;
    font-size: rem-calc(20);
  }

  &__document {
    a {
      margin: 0;
      .icon {
        font-size: rem-calc(32);
        line-height: rem-calc(16);
        &.icon-download:before {
          margin-left: rem-calc(-7);
        }
      }
    }
  }

  @media #{$small-only} {
    display: table;
    &__document {
      display: table-footer-group;
      a {
        margin-top: rem-calc(15);
      }
    }
  }

  @media #{$medium-up} {
    position: relative;
    padding-top: rem-calc(40);
    margin-bottom: rem-calc(30);

    &__title {
      font-size: rem-calc(38);
      line-height: 130%;
      &--sub {
        font-size: rem-calc(26);
      }
      &--limit {
        width: 65%;
      }
    }

    &__document {
      position: absolute;
      top: 50%;
      right: 0;
      @include transform(translateY(-50%));

      ~ .heading__title {
        padding-right: rem-calc(175);
      }
    }
  }
}

.heading--bordered {
  &:before {
    content: "";
    @include position(absolute, 0 null null 0);
    width: rem-calc(40);
    height: rem-calc(5);
    background: $jet;
    @include flex-basis(0);
  }

  @media #{$medium-up} {
    padding-top: rem-calc(24);
    &:before {
      width: rem-calc(50);
      height: rem-calc(7);
    }
  }
}

.heading--small {
  .heading__title {
    font-size: rem-calc(28*2/3);
    @media #{$medium-up} {
      font-size: rem-calc(18);
    }
    @media #{$large-up} {
      font-size: rem-calc(24);
    }
  }

  .heading__title--sub {
    font-size: rem-calc(28*2/3);
    @media #{$medium-up} {
      font-size: rem-calc(15);
    }
    @media #{$large-up} {
      font-size: rem-calc(20);
    }
  }
}


.heading--white {
  max-width: rem-calc(700);

  &:before {
    background: $white;
  }

  .heading__title {
    color: $white;
    line-height: 130%;
    font-size: rem-calc(36*2/3);
  }

  .heading__title--sub {
    font-size: rem-calc(36*2/3);
    color: rgba($white, .5);
  }

  .heading__title--sub strong {
    color: $white;
  }

  .heading__title--sub--link {
    display: inline-block;
    margin: 0 0 rem-calc(20) rem-calc(10);
    color: $white;
    border-bottom: 2px solid $white;
  }

  @media #{$medium-up} {
    .heading__title {
      font-size: rem-calc(38);
    }

    .heading__title--sub {
      font-size: rem-calc(38);
    }
  }
}

.heading--tiny {
  text-align: center;
  color: $jet;
  font-size: rem-calc(30*2/3);
  font-family: $font-bold;
  font-weight: $font-weight-normal;
  margin-bottom: rem-calc(30*2/3);

  @media #{$medium-up} {
    font-size: rem-calc(26);
  }
}

.heading--large {
  .heading__title {
    font-size: rem-calc(54*2/3);
    font-family: $font-bold;
    font-weight: $font-weight-normal;
    margin-bottom: rem-calc(30*2/3);
  }
  @media #{$medium-up} {
    .heading__title {
      font-size: rem-calc(50);
      margin-bottom: 0;
    }
  }
}


// Phone
.phone-green {
  color: $green;
  font-family: $font-bold;
  font-weight: $font-weight-normal;
  text-align: center;
  display: block;
  font-size: rem-calc(39*2/3);
  margin: rem-calc(30*2/3) 0;
  span {
    opacity: .5;
  }

  .no-touch &:hover,
  .touch &:active,
  &:focus {
    color: $green !important;
  }

  @media #{$medium-up} {
    text-align: left;
  }
}


.legalNote {
  color: $grey;
  font-size: rem-calc(14);
  padding: rem-calc(20) 0;
  p {
    margin: 0;
  }

  @media #{$medium-up} {
    font-size: rem-calc(12);
  }
}

.legalMentions {
  color: $mediumGrey;
  font-style: italic;
  font-size: rem-calc(14);

  @media #{$medium-up} {
    padding: rem-calc(10) 22% 0 0;
  }
}