table {
  border: none;
  background: transparent;
  width: 100%;
  border-spacing: 0;
  margin-bottom: 0;
  word-wrap:break-word;

  tr td {
    white-space: normal;
    padding: rem-calc(7);
  }

  tr:nth-of-type(even) {
    background: transparent;
  }

  [align="center"] {
    text-align: center;
  }

  @media #{$medium-up} {
    tr td {
      padding: rem-calc(10);
    }
  }
}

input[type="checkbox"] {
  margin-bottom: 0;
  margin-right: rem-calc(10);
}

label {
  @include display(flex);
  @include align-items(baseline);
  font-size: rem-calc(14);

  @media #{$medium-up} {
    font-size: rem-calc(16);

    & + br {
      display: none;
    }
  }
}

form {
  width: 50%;
  margin: 0 auto;

  & > table > tbody > tr > td {
    padding: 0;
  }

  @media #{$small-only} {
    width: 100%;
  }
}

.tdg,
.td0 {
  background: $lightGrey;
}

table.tab1 {
  border: 1px solid $lightGrey;
}

.td0 {
  @extend .heading__title;
  font-size: rem-calc(28*2/3);
  @media #{$medium-up} {
    font-size: rem-calc(18);
  }
  @media #{$large-up} {
    font-size: rem-calc(24);
  }
}

.td1 {
  font-size: rem-calc(14);
  width: 50%;

  div {
    float: none !important;
    display: inline-block;
    vertical-align: middle;
    width: 40%;
  }
  div + select {
    width: 58%;
    margin-bottom: 0;
    float: none !important;
    display: inline-block;
    vertical-align: middle;
  }

  @media #{$medium-up} {
    font-size: rem-calc(16);
    width: 50%;

    div {
      width: 49%;
    }

    div + select {
      width: 49%;
    }
  }
}


// main title
.titre1 {
  display: block;
  @extend .heading__title;
  text-align: center;

  font-size: rem-calc(28*2/3);
  @media #{$medium-up} {
    font-size: rem-calc(18);
  }
  @media #{$large-up} {
    font-size: rem-calc(24);
  }
}



// form fields
input[type="button"]{
  @extend .btn-primary;
  @extend .btn-rounded;
  margin-bottom: 0;
}
.put1 {
  margin-bottom: 0 !important;
  width: 90% !important;
  background-color: $white;
  vertical-align: middle;
  display: inline-block !important;

  @media #{$small-only} {
    &[name="ZoneABC"] {
      margin-bottom: rem-calc(5) !important;
    }
  }


  &[name="ZoneABC"],
  &[name="BtnZonier"] {
    width: 90% !important;
    display: block;
  }

  @media #{$medium-up} {
    &[name="ZoneABC"] {
      width: 60% !important;
    }
    &[name="BtnZonier"] {
      width: 30% !important;
      padding-left: rem-calc(5);
      padding-right: rem-calc(5);
    }
  }
}

.Note1,
div.note1 {
  background: none;
  text-align: right;
  font-size: rem-calc(12);
  color: $grey;
  display: block;
  padding-top: rem-calc(20);
  a {
    color: $grey;
  }
}



// FNFG:
// Montant estim
// des frais de notaire
// sur lacquisition :
.td1r {
  color: $pink;
  font-family: $font-bold;
  font-size: rem-calc(20);
}

// main title
td {
  .titre1 {
    padding: rem-calc(20) 0;
    background: $white;
  }
}

[name="BtnSubmit"] {
  margin-bottom: rem-calc(20) !important;
  margin-top: rem-calc(20) !important;
}



// PTZ
#AltoPtz {
  form {
    width: 100%;
  }

  [name="BtnSubmit"] {
    margin-bottom: 0 !important;
    margin-top: 0 !important;
  }
}

#PtzResult {
  background: $lightGrey;
}

#PtzInfoRFR {
  color: $grey;
  font-size: rem-calc(12);
  padding-top: rem-calc(20);
  margin-bottom: rem-calc(20);
}

#PtzInput {
  margin-bottom: rem-calc(20);
}

.td1p {
  width: 5%;
}

@media #{$medium-up} {
  #PtzInput {
    width: 49%;
    float: left;
    margin-bottom: 0;
  }

  #PtzResult {
    width: 49%;
    float: right;
  }

  #PtzInfoRFR {
    clear: both;
    width: 70%;
    font-size: rem-calc(14);
  }
}

// main title
#PtzTitre {
  padding: rem-calc(20) 0;

  br {
    display: none;
  }
}

#PtzIfRevSup1,
#PtzIfQuestion,
#PtzIfQuestion {
  width: 100%;
}

#PtzIsShowBrm1 {
  overflow: auto;

  @media #{$small-only} {
    width: 100vw;
  }
}

#PtzIfRevSup0 {
  width: 100%;
}

#PtzIdNbPersMenage {
  color: $pink !important;
  margin-left: rem-calc(10);
}

#PtzIsShowBrm1 {
  [align="center"] td {
    text-align: center;
  }
  table {
    word-wrap: normal;
  }
  td {
    border: 1px solid $lightGrey;
    white-space: nowrap;

    div {
      width: 100%;
    }
  }

  .td2 {
    background: $darkBlue;
    border: none;
    color: $white;
    font-family: $font-bold;
    border: 1px solid $white;
  }
}

.td1t {
  background: $darkBlue;
  color: $white;
  font-family: $font-bold;
}

input[name="BtnSubmit"] {
  height: auto !important;
}