@media #{$medium-only} {
  body {
    overflow-x: hidden;
  }

  @media #{$portrait} {
    html, body {
      font-size: 80%
    }
  }
}

@media #{$small-only} {
  body {
    overflow-x: hidden;
  }
}


// WRAPPERS
// main site wrapper
.main-wrapper {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  position: relative;

  @media #{$medium-up} {
    overflow: inherit;
    max-width: $xlarge-breakpoint;
    min-width: em-calc(900);
    padding-left: 0;
    padding-right: 0;
  }
}

// large content wrapper
.wrapper {
  width: 100%;
  padding-left: $column-gutter-small;
  padding-right: $column-gutter-small;
  margin: 0 auto;
}
.wrapper--medium-up {
  padding-left: 0;
  padding-right: 0;
  margin: 0 auto;
  width: 100%;
}
.wrapper--narrow {
  @extend .wrapper;
  padding-left: ($column-gutter-small / 2);
  padding-right: ($column-gutter-small / 2);
}

@media #{$medium-only} {
  .wrapper,
  .wrapper--medium-up {
    padding-left: ($column-gutter * 2);
    padding-right: ($column-gutter * 2);
  }
}

@media #{$large-up} {
  .wrapper,
  .wrapper--medium-up {
    max-width: em-calc(1220);
    padding-left: $column-gutter;
    padding-right: $column-gutter;
  }
}

// small wrapper to center block
@media #{$small-only} {
  .small-wrapper {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
}


// section spacing
.section-padding {
  padding-top: $small-section-padding;
  padding-bottom: $small-section-padding;

  @media #{$medium-up} {
    padding-top: $medium-section-padding;
    padding-bottom: $medium-section-padding;
  }
}

// prevent padding top on body (really bad)
.f-topbar-fixed {
  //padding-top: 0 !important;
  padding-top: $topbar-height;

  #container {
    //padding-top: $topbar-height;
  }
}

// small headers
.narrow-header {
  position: relative;
  min-height: rem-calc(400*2/3);
  padding-top: rem-calc(130*2/3);
  background-size: cover;
  background-position: center center;

  .heading {
    padding-bottom: rem-calc(100*2/3);
    margin-bottom: 0;
  }


  @media #{$medium-up} {
    min-height: rem-calc(400);
    padding-top: rem-calc(130);
  }
}


// centered content
.content-centered {
  margin: rem-calc(15*2/3) auto;

  @media #{$medium-up} {
    width: 45%;
    margin: rem-calc(30) auto;
  }
}

.blockText-centered {
  padding: rem-calc(40*2/3) 10%;
  margin-bottom: rem-calc(80*2/3);
  text-align: center;

  &__spaced {
    margin-bottom: rem-calc(40*2/3);

    p:last-child {
      margin-bottom: 0;
    }
  }

  @media #{$medium-up} {
    padding: rem-calc(50) 20%;
    margin-bottom: rem-calc(80);
    &__spaced {
      margin-bottom: rem-calc(30);
    }
  }
}


// remove lateral paddings on .row elements
.row--noGutter {
  .columns {
    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }
}


// swap item trick
@media #{$medium-up} {
  .swapItem {
    width: 100%;
    display: table;

    &__1 {
      display: table-header-group;
    }
    &__2 {
      display: table-row-group;
    }
    &__3 {
      display: table-footer-group;
    }
  }
}


// media
.media {
  @include display(flex);

  &__img {
    margin-right: rem-calc(20*2/3);
    img {
      display: block;
    }
  }

  &__content {
    @include flex(2 1 auto);
  }
}


// flex content
@media #{$medium-up} {
  .flex-content {
    font-size: 0;
    margin-left: -1% !important;
    margin-right: -1% !important;

    > * {
      display: inline-block;
      width: 31.3333%;
      margin-left: 1% !important;
      margin-right: 1% !important;
    }
  }
}
