// INPUT (Foundation default styles are unconvenient)
#{$all-text-inputs} {
  height: $form-height;
  padding: 0 $form-padding;
  font-size: $input-font-size;
  font-family: $font-regular;
  font-weight: $font-weight-normal;
  border: $input-border-width $input-border-style $input-border-color;

  @include placeholder {
    color: $grey;
    font-family: $font-regular;
    font-weight: $font-weight-normal;
  }

  &.small-padding {
    padding-left: rem-calc(5);
    padding-right: rem-calc(5);
  }

  @include focus();
}

select {
  background-color: $white;
  background-image: url(data:image/svg+xml;base64,);
  padding-right: rem-calc(30);

  &:focus {
    background-color: $white;
    border-color: $blue;
    outline: none;
  }
}


// remove Drupal margins start
.form-item, .form-actions {
  margin-top: 0;
  margin-bottom: 0;
}
// remove Drupal margins end

.webform-component {
  label {
    color: $blue;
    font-family: $font-bold;
    padding: rem-calc(8) 0;
  }

  .form-text,
  .form-textarea {
    border: 2px solid $input-border-color;
  }

  .form-textarea {
    height: rem-calc(150);
  }

  .label-checkbox {
    > span {
      color: $grey;
      font-size: rem-calc(14);
    }
  }

  // input type file
  .form-managed-file {
    position: relative;
    @include display(flex);
    width: 100%;

    input {
      margin-bottom: 0;
      height: rem-calc(45);
    }

    input[type=file] {
      position: absolute;
      left: 0;
      top: 0;
      background: transparent;
      opacity: .01;
    }

    [data-fake-file] {
      margin-right: rem-calc(10);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    input[type=file],
    [data-fake-file] {
      cursor: pointer;
      background: $mediumGrey;
      height: rem-calc(45);
      font-size: rem-calc(14);
      @include flex-grow(1);
    }

    input[type='submit'] {
      position: relative;
      z-index: 1;
    }

    + .description {
      display: none;
    }
  }

  &.webform-container-inline {
    margin-bottom: rem-calc(20);
  }


  @media #{$medium-up} {
    label {
      padding: rem-calc(15) 0;
    }

    &.webform-container-inline {
      margin-bottom: rem-calc(10);

      label,
      .combo-select {
        display: inline-block;
        width: auto;
        vertical-align: middle;
      }

      label {
        margin-right: rem-calc(20)
      }

      .combo-select {
        input {
          margin-bottom: 0;
        }
      }
    }

    .form-managed-file {
      @include display(inline-flex);
      @include flex-direction(row);
      @include align-items(center);

      width: auto;
      input[type=file],
      [data-fake-file] {
        width: rem-calc(250);
      }
      .file {
        margin-right: rem-calc(10);
      }
    }
  }
}

.form-actions {
  text-align: center;
  margin-top: rem-calc(20);
}
input[type="submit"].form-submit {
  @extend .btn-primary;
  @extend .btn-rounded;
}


// ERROR
.form-item {
  input.error,
  textarea.error,
  select.error,
  select.error + * + * + input {
    border-color: $alert-color;
  }
}
.error {
  .error,
  .field__input {
    color: $alert-color !important;
  }
  small.error {
    text-transform: none !important;
  }
}

.messages.error,
.form-required {
  color: $alert-color;
}


// CHECKBOX
.input-checkbox {
  @extend .visually-hidden;

  &:checked + label:before {
    background: $blue;
    box-shadow: inset 0 0 0 2px $white;
  }
  &:focus + label:before {
    box-shadow: 0 0 4px 0 $glowing-effect-color,
                inset 0 0 0 2px $white;
  }
}

.label-checkbox {
  @include display(flex !important);
  @include align-items(flex-start);
  line-height: 1;
  padding-top: rem-calc(10);
  padding-bottom: rem-calc(10);
  padding-right: rem-calc(10);
  cursor: pointer;
  position: relative;

  @media #{$medium-up} {
    padding-top: rem-calc(5);
    padding-bottom: rem-calc(5);
    padding-right: rem-cacl(20);
  }

  &.only-text {
    @include align-items(center);
    display: inline-block;
    padding-left: rem-calc(16);
  }

  &:before {
    content: "";
    display: inline-block;
    width: rem-calc(30*2/3);
    height: rem-calc(30*2/3);
    min-width: rem-calc(30*2/3);
    min-height: rem-calc(30*2/3);
    border: 1px solid $blue;
    background: $white;
    position: absolute;
    left: 0;
    top: 50%;
    @include transform(translateY(-50%));

    @media #{$medium-up} {
      width: rem-calc(15);
      height: rem-calc(15);
      min-width: rem-calc(15);
      min-height: rem-calc(15);
    }
  }

  > span {
    padding-left: rem-calc(40*2/3);
    display: block;
    @media #{$medium-up} {
      padding-left: rem-calc(25);
    }
  }
}

input[type="checkbox"] + .label-checkbox {
  margin-right: 0;
  margin-left: 0;
}


// AJAX FORM
[data-ajax-form] {
  position: relative;

  [data-ajax-form-response] {
    opacity: 0;
    display: none;
    @include flex-direction(column);
    @include justify-content(center);
    @include position(absolute, 0 0 0 0);
    z-index: 1;
    text-align: center;

    @include transition-property(all);
    @include transition-duration(.4s);
    @include transition-timing-function(ease-out);

    p {
      font-family: $font-bold;
    }
  }

  [data-ajax-form-content] {
    opacity: 1;
  }
}

.status-ok[data-ajax-form] {
  [data-ajax-form-response] {
    opacity: 1;
    background: $white;
    @include display(flex);

    p {
      color: $success-color;
    }
  }
}

.status-nok[data-ajax-form] {
  [data-ajax-form-response] {
    opacity: 1;
    background: rgba($black, .8);
    @include display(flex);

    p {
      color: $alert-color;
    }
  }
}

.emailForm {
  &__title {
    p {
      margin: 0;
      font-size: rem-calc(20);
      font-family: $font-bold;
      color: $jet;
    }

    @media #{$small-only} {
      padding-bottom: rem-calc(20*2/3);
    }
  }

  &__input {
    label {
      position: relative;
    }

    input {
      padding-right: rem-calc(38);
    }

    .button-submit {
      position: absolute;
      padding: 0;
      top: rem-calc(18);
      right: rem-calc(16);
      font-size: rem-calc(16);
      color: $grey;
      background-color: transparent;
      border: none;
    }
  }

  @media #{$medium-up} {
    @include display(flex);

    &__title {
      width: rem-calc(320);

      p {
        padding-right: rem-calc(20);
        line-height: rem-calc(25);
      }
    }

    &__input {
      width: 100%;
      max-width: rem-calc(540);

      input {
        margin: 0;
      }
    }
  }
}
